<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <resize-table>
        <table class="table">
          <thead>
            <tr>
              <th width="25%">年级</th>
              <th width="25%">年级组长</th>
              <th width="25%">年级副组长</th>
              <th width="25%">操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in table.list">
              <td>{{item.name}}</td>
              <td>
                <p v-if="item.gradeLeader">{{item.gradeLeader.name}}</p>
                <p v-else>--</p>
              </td>
              <td>
                <div v-if="!!item.viceLeaders && item.viceLeaders.length > 0">
                <span v-for="(i,k) in item.viceLeaders">
                  {{i.name}}<span v-show="k !== item.viceLeaders.length - 1">，</span>
                </span>
                </div>
                <p v-else>--</p>
              </td>
              <td>
                <span @click="showDialog(item)" class="oper">编辑</span>
              </td>
            </tr>
          </tbody>
        </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
      <el-dialog width="540px" :visible.sync="dialog" class="dialog" title="编辑">
        <div class="item">
          <span class="title">年级名称</span>
          <el-input v-model="form.name" disabled class="input"></el-input>
        </div>
        <div class="item">
          <span class="title">年级组长</span>
          <el-select
              v-model="form.leaderId"
              class="input"
              filterable
              default-first-option
              remote
              clearable
              reserve-keyword
              placeholder="请输入教职工关键词"
              :remote-method="getStaff"
              :loading="loading">
            <el-option
                v-for="item in staffList"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              <span style="float: left">{{ item.name }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{item.idCard}}/{{item.phone}}</span>
            </el-option>
          </el-select>
        </div>
        <div class="item">
          <span class="title">副组长</span>
          <el-select
              v-model="form.viceLeaderIds"
              class="input"
              multiple
              filterable
              default-first-option
              remote
              clearable
              reserve-keyword
              placeholder="请输入教职工关键词"
              :remote-method="getStaff"
              :loading="loading">
            <el-option
                v-for="item in staffList"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              <span style="float: left">{{ item.name }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{item.idCard}}/{{item.phone}}</span>
            </el-option>
          </el-select>
        </div>
        <div class="btns">
          <div @click="doSubmit()" class="btn-submit btn-edit mr15">提交</div>
          <div @click="dialog = false" class="btn-clear">取消</div>
        </div>
      </el-dialog>
    </main-part>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        assistantIds: [],
        nav: [{name: "年级管理", path: ""}],
        table: {
          url: '/grade/queryGradePage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {}
        },
        dialog: false,
        // 所有的教职工
        staffList: [],
        loading: false,
        form: {
          name: '',
          leaderId: '',
          viceLeaderIds: []
        },
      }
    },
    methods: {
      // 展示弹窗
      async showDialog(item) {
        this.dialog = true
        this.form = {
          gradeId: item.gradeId,
          name: item.name,
          leaderId: item.gradeLeader ? item.gradeLeader.id : null,
          viceLeaderIds: []
        }
        await this.getStaff()
        if (item.viceLeaders) {
          item.viceLeaders.forEach(e => {
            this.staffList.push(e)
          })
        }
        if (item.gradeLeader) {
          this.staffList.push(item.gradeLeader)
        }
        if (item.viceLeaders && item.viceLeaders.length) {
          this.form.viceLeaderIds = item.viceLeaders.map(e => e.id)
        }
      },
      // 编辑年级
      async doSubmit() {
        await this.$ajax({
          url: '/grade/updateGradeLeader.do',
          el: '.btn-edit',
          noText: true,
          query: this.form
        })
        this.$remind('修改成功')
        this.dialog = false
        this.$search(this.table)
      },
      // 获取教职工成员
      async getStaff(query) {
        if (query !== '') {
          this.loading = true
          let res = await this.$ajax({
            url: '/user/queryStaffPage.do',
            query: {
              keywords: query,
              pageNo: 1,
              pageSize: 15
            }
          })
          this.loading = false
          this.staffList = res.list
        } else {
          this.staffList = []
        }
      }
    },
    mounted() {
      this.$search(this.table)
      this.getStaff()
    }
  }
</script>
<style lang="sass" scoped>
  .dialog
    .title
      width: 70px
      line-height: 40px
      vertical-align: top
      display: inline-block
    .input
      width: calc(100% - 70px)
    .item
      margin-bottom: 20px
    .btns
      text-align: center
</style>
